import * as React from "react"
import { navigate } from "gatsby"
import { BannerModuleStyles } from "./BannerModuleStyles"
import { Grid } from "@mui/material"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

export const ArtworkBanner = ({ artwork }) => {
    const {
        title, minimumPrice, introduction, year, picture, color, medium, height, width,
    } = artwork

    // noinspection JSUnusedLocalSymbols
    function scrollToArea() {
        // noinspection JSIgnoredPromiseFromCall
        navigate("#topContent")
    }

    const image = getImage(artwork.gatsbyImage)

    return (<>
            <BannerModuleStyles>
                <GatsbyImage
                    className="banner__image"
                    imgClassName="banner__image--content"
                    image={image}
                    alt="SKP - Searching for dopamine"
                />

                <div className="container">

                    <div className="banner__content">
                        <Grid container spacing={8}>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                <br />
                                <h1>
                                    {title}
                                    <span style={{ color: "var(--primary)" }}>.</span>
                                </h1>
                                <div className={"artwork-title"}>{title}, {year}</div>
                                <div
                                    className={"artwork-type"}>{color.map((c, i) => {
                                    return c.name + (i < color.length - 1 && " and " || "")
                                })} on {medium.name}</div>
                                <div className={"artwork-size"}>{height} x {width} cm</div>
                                <div
                                    className={"artwork-size"}>{Math.round(height / 2.54)} x {Math.round(width / 2.54)} in
                                </div>
                                <div className={"artwork-classification"}>This is a unique work.</div>
                                <div className={"artwork-price"}>{minimumPrice} â‚¬</div>
                                <div className={"artwork-description"}>This work includes a certificate of authenticity.
                                </div>
                            </Grid>
                            <Grid item xl={6} lg={6} md={6} sm={12} xs={12}>
                                <img src={picture.to_framed_path()} alt={title}
                                     style={{ maxWidth: "467px", maxHeight: "600px" }} />
                            </Grid>
                        </Grid>

                    </div>
                </div>
                <div className="gradient"></div>
            </BannerModuleStyles>
            <span id="topContent"></span>
        </>)
}
